<script>
	/*每个页面公共js */
	export default {
		//项目加载
		onLaunch: function() {
			uni.http = "http://127.0.0.1:82";//缓存平台请求网址
			this.getlabeldata();//缓存平台所有活动数据
			this.getactidata();//缓存平台所有标签数据
			uni.getSystemInfo({//缓存用户登录设备数据
				success: res =>{
					uni.app = res;
				}
			})
			// console.log(uni);
		},
		methods:{
			async getlabeldata(){
				const res = await this.$myRequest({
					url:'/api/label/getwholedata',
				})
				console.log("加载了标签");
				uni.label = res.data;
			},
			async getactidata(){
				const res = await this.$myRequest({
					url:'/api/acti/getwholedata',
					data:{
						'field':'id,shopimg',
					},
				})
				console.log("加载了活动");
				uni.acti = res.data;
			}
		},
		//项目显示
		onShow: function() {
			console.log('App Show')
		},
		//项目离开时
		onHide: function() {
			console.log('App Hide')
		},
	}
	
</script>

<style>
/*引入公共css */
@import url("/common/css/common.css");
</style>
